import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      redirect: '/login'
    },
    {
      path: '/home',
      component: resolve => require(['../components/pages/Home.vue'], resolve),
      children: [
        {
          path: '/',
          component: resolve => require(['../components/pages/Dashboard.vue'], resolve)
        },
        {
          path: '/websetting',
          component: resolve => require(['../components/pages/Websetting.vue'], resolve)
        },
        {
          path: '/seosetting',
          component: resolve => require(['../components/pages/Seosetting.vue'], resolve)
        },
        {
          path: '/advertisement',
          component: resolve => require(['../components/pages/Advertisement.vue'], resolve)
        },
        {
          path: '/blogroll',
          component: resolve => require(['../components/pages/Blogroll.vue'], resolve)
        },
        {
          path: '/user',
          component: resolve => require(['../components/pages/User.vue'], resolve),
          beforRouteEnter (to, from, next) {
            this.data = { aaa: 'aaa' }
            next(this.data)
          }
        },
        {
          path: '/authority',
          component: resolve => require(['../components/pages/Authority.vue'], resolve)
        },
        {
          path: '/group',
          component: resolve => require(['../components/pages/Group.vue'], resolve)
        },
        {
          path: '/review',
          component: resolve => require(['../components/pages/Review.vue'], resolve)
        },
        {
          path: '/sort',
          component: resolve => require(['../components/pages/Sort.vue'], resolve)
        },
        {
          path: '/novel',
          component: resolve => require(['../components/pages/Novel.vue'], resolve)
        },
        {
          path: '/novel_new',
          component: resolve => require(['../components/pages/NovelNew.vue'], resolve)
        },
        {
          path: '/novel_edit',
          component: resolve => require(['../components/pages/NovelEdit.vue'], resolve)
        },
        {
          path: '/novel_chapter',
          component: resolve => require(['../components/pages/NovelChapter.vue'], resolve)
        },
        {
          path: '/chapter',
          component: resolve => require(['../components/pages/Chapter.vue'], resolve)
        },
        {
          path: '/chapter_edit',
          component: resolve => require(['../components/pages/ChapterEdit.vue'], resolve)
        },
        {
          path: '/chapter_new',
          component: resolve => require(['../components/pages/ChapterNew.vue'], resolve)
        },
        {
          path: '/read',
          component: resolve => require(['../components/pages/Read.vue'], resolve)
        }
      ]
    },
    {
      path: '/login',
      component: resolve => require(['../components/pages/Login.vue'], resolve)
    }
  ]
})

// 这里可以设置router对象的各种动作
router.beforeEach((to, from, next) => {
  let accessToken = sessionStorage.getItem('access_token')
  if (to.path === '/login') {
    next()
  } else {
    if (accessToken === null) {
      router.push('/login')
    } else {
      next()
    }
  }
})

export default router



