// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import VueResource from 'vue-resource'
import iView from 'iview'
import 'iview/dist/styles/iview.css'

Vue.use(VueResource)
Vue.use(iView)

Vue.config.debug = true
Vue.config.productionTip = false
Vue.http.options.emulateJSON = true// 异步相关

// 保存header请求头
Vue.http.interceptors.push(function (request, next) {
  let accessToken = sessionStorage.getItem('access_token', null)
  if (accessToken !== null) {
    request.headers.set('Authorization', accessToken)
  }

  next(function (response) {
    // 登录验证
    if (response.body.error === 401) {
      this.$router.push('/login')
    }

    // 无权访问则后退
    if (response.body.error === 403) {
      this.$router.go(-1)
    }

    // 错误验证
    if (!response.ok) {
      this.$Notice.error({ title: '服务器似乎不在状态', desc: '错误代码:' + response.status + '<br>错误信息:' + response.statusText })
    }
  })
})

/* eslint-disable no-new */
const app = new Vue({
  router,
  render: h => h(App)
}).$mount('#app', app)



